/**
 * Bulk Loading Performance Monitor
 * 
 * åŠŸèƒ½ï¼š
 * - ç›‘æ§æ‰¹é‡åŠ è½½æ€§èƒ½
 * - è·Ÿè¸ªç¼“å­˜æ•ˆæœ
 * - æ€§èƒ½æŒ‡æ ‡æ”¶é›†å’ŒæŠ¥å‘Š
 * - è‡ªåŠ¨ä¼˜åŒ–å»ºè®®
 */

export class BulkLoadingPerformanceMonitor {
  constructor() {
    this.metrics = {
      // åŠ è½½æ€§èƒ½
      loadingStartTime: null,
      loadingEndTime: null,
      totalLoadingTime: 0,
      
      // æ•°æ®ç»Ÿè®¡
      plantsLoaded: 0,
      annotationsLoaded: 0,
      notesLoaded: 0,
      
      // ç½‘ç»œæ€§èƒ½
      networkRequests: 0,
      bulkRequests: 0,
      individualRequests: 0,
      totalDataTransferred: 0,
      
      // ç¼“å­˜æ€§èƒ½
      cacheHits: 0,
      cacheMisses: 0,
      cacheSize: 0,
      
      // é”™è¯¯ç»Ÿè®¡
      errors: [],
      fallbacksUsed: 0,
      
      // ç”¨æˆ·ä½“éªŒ
      timeToFirstInteraction: 0,
      timeToFullyLoaded: 0
    };
    
    this.isMonitoring = false;
    this.startTime = null;
    this.checkpoints = [];
  }

  /**
   * å¼€å§‹ç›‘æ§åŠ è½½è¿‡ç¨‹
   */
  startMonitoring() {
    this.isMonitoring = true;
    this.startTime = performance.now();
    this.metrics.loadingStartTime = Date.now();
    this.checkpoints = [];
    
    console.log('[æ€§èƒ½ç›‘æ§] å¼€å§‹ç›‘æ§æ‰¹é‡åŠ è½½è¿‡ç¨‹');
  }

  /**
   * æ·»åŠ æ£€æŸ¥ç‚¹
   */
  addCheckpoint(name, details = {}) {
    if (!this.isMonitoring) return;
    
    const checkpoint = {
      name,
      timestamp: performance.now(),
      relativeTime: performance.now() - this.startTime,
      details
    };
    
    this.checkpoints.push(checkpoint);
    console.log(`[æ€§èƒ½ç›‘æ§] ${name}: ${checkpoint.relativeTime.toFixed(2)}ms`, details);
  }

  /**
   * è®°å½•æ•°æ®åŠ è½½å®Œæˆ
   */
  recordDataLoaded(type, count, dataSize = 0) {
    switch (type) {
      case 'plants':
        this.metrics.plantsLoaded = count;
        break;
      case 'annotations':
        this.metrics.annotationsLoaded = count;
        break;
      case 'notes':
        this.metrics.notesLoaded = count;
        break;
    }
    
    this.metrics.totalDataTransferred += dataSize;
    this.addCheckpoint(`${type} æ•°æ®åŠ è½½å®Œæˆ`, { count, dataSize: `${(dataSize / 1024).toFixed(2)}KB` });
  }

  /**
   * è®°å½•ç½‘ç»œè¯·æ±‚
   */
  recordNetworkRequest(type, isBulk = false) {
    this.metrics.networkRequests++;
    
    if (isBulk) {
      this.metrics.bulkRequests++;
    } else {
      this.metrics.individualRequests++;
    }
    
    this.addCheckpoint(`ç½‘ç»œè¯·æ±‚: ${type}`, { isBulk, total: this.metrics.networkRequests });
  }

  /**
   * è®°å½•ç¼“å­˜å‘½ä¸­
   */
  recordCacheHit(type, cacheSize = 0) {
    this.metrics.cacheHits++;
    this.metrics.cacheSize = cacheSize;
    
    console.log(`[ç¼“å­˜å‘½ä¸­] ${type} - æ€»å‘½ä¸­: ${this.metrics.cacheHits}`);
  }

  /**
   * è®°å½•ç¼“å­˜æœªå‘½ä¸­
   */
  recordCacheMiss(type) {
    this.metrics.cacheMisses++;
    
    console.log(`[ç¼“å­˜æœªå‘½ä¸­] ${type} - æ€»æœªå‘½ä¸­: ${this.metrics.cacheMisses}`);
  }

  /**
   * è®°å½•é”™è¯¯
   */
  recordError(error, context = '') {
    this.metrics.errors.push({
      error: error.message,
      context,
      timestamp: Date.now()
    });
    
    console.error(`[æ€§èƒ½ç›‘æ§] é”™è¯¯è®°å½•: ${context}`, error);
  }

  /**
   * è®°å½•å›é€€ä½¿ç”¨
   */
  recordFallback(reason) {
    this.metrics.fallbacksUsed++;
    
    this.addCheckpoint('ä½¿ç”¨å›é€€æ–¹æ¡ˆ', { reason, totalFallbacks: this.metrics.fallbacksUsed });
  }

  /**
   * ç»“æŸç›‘æ§å¹¶ç”ŸæˆæŠ¥å‘Š
   */
  endMonitoring() {
    if (!this.isMonitoring) return null;
    
    this.isMonitoring = false;
    this.metrics.loadingEndTime = Date.now();
    this.metrics.totalLoadingTime = performance.now() - this.startTime;
    
    const report = this.generatePerformanceReport();
    console.log('[æ€§èƒ½ç›‘æ§] åŠ è½½å®Œæˆï¼Œç”Ÿæˆæ€§èƒ½æŠ¥å‘Š:', report);
    
    return report;
  }

  /**
   * ç”Ÿæˆæ€§èƒ½æŠ¥å‘Š
   */
  generatePerformanceReport() {
    const cacheHitRate = this.metrics.cacheHits + this.metrics.cacheMisses > 0 
      ? (this.metrics.cacheHits / (this.metrics.cacheHits + this.metrics.cacheMisses) * 100).toFixed(1)
      : 0;

    const bulkRequestRatio = this.metrics.networkRequests > 0
      ? (this.metrics.bulkRequests / this.metrics.networkRequests * 100).toFixed(1)
      : 0;

    const report = {
      // æ€»ä½“æ€§èƒ½
      summary: {
        totalLoadingTime: `${this.metrics.totalLoadingTime.toFixed(2)}ms`,
        dataLoaded: {
          plants: this.metrics.plantsLoaded,
          annotations: this.metrics.annotationsLoaded,
          notes: this.metrics.notesLoaded
        },
        networkRequests: this.metrics.networkRequests,
        dataTransferred: `${(this.metrics.totalDataTransferred / 1024).toFixed(2)}KB`
      },
      
      // ç½‘ç»œæ€§èƒ½
      networkPerformance: {
        totalRequests: this.metrics.networkRequests,
        bulkRequests: this.metrics.bulkRequests,
        individualRequests: this.metrics.individualRequests,
        bulkRequestRatio: `${bulkRequestRatio}%`,
        averageRequestSize: this.metrics.networkRequests > 0 
          ? `${(this.metrics.totalDataTransferred / this.metrics.networkRequests / 1024).toFixed(2)}KB`
          : '0KB'
      },
      
      // ç¼“å­˜æ€§èƒ½
      cachePerformance: {
        hits: this.metrics.cacheHits,
        misses: this.metrics.cacheMisses,
        hitRate: `${cacheHitRate}%`,
        cacheSize: `${(this.metrics.cacheSize / 1024).toFixed(2)}KB`
      },
      
      // é”™è¯¯å’Œå›é€€
      reliability: {
        errors: this.metrics.errors.length,
        fallbacksUsed: this.metrics.fallbacksUsed,
        errorDetails: this.metrics.errors
      },
      
      // æ£€æŸ¥ç‚¹æ—¶é—´çº¿
      timeline: this.checkpoints,
      
      // æ€§èƒ½è¯„çº§
      performanceGrade: this.calculatePerformanceGrade(),
      
      // ä¼˜åŒ–å»ºè®®
      optimizationSuggestions: this.generateOptimizationSuggestions()
    };

    return report;
  }

  /**
   * è®¡ç®—æ€§èƒ½è¯„çº§
   */
  calculatePerformanceGrade() {
    let score = 100;
    
    // åŠ è½½æ—¶é—´è¯„åˆ†
    if (this.metrics.totalLoadingTime > 5000) {
      score -= 30; // è¶…è¿‡5ç§’æ‰£30åˆ†
    } else if (this.metrics.totalLoadingTime > 2000) {
      score -= 15; // è¶…è¿‡2ç§’æ‰£15åˆ†
    } else if (this.metrics.totalLoadingTime > 1000) {
      score -= 5; // è¶…è¿‡1ç§’æ‰£5åˆ†
    }
    
    // ç½‘ç»œè¯·æ±‚è¯„åˆ†
    const requestEfficiency = this.metrics.bulkRequests / (this.metrics.networkRequests || 1);
    if (requestEfficiency < 0.5) {
      score -= 20; // æ‰¹é‡è¯·æ±‚æ¯”ä¾‹ä½äº50%æ‰£20åˆ†
    } else if (requestEfficiency < 0.8) {
      score -= 10; // æ‰¹é‡è¯·æ±‚æ¯”ä¾‹ä½äº80%æ‰£10åˆ†
    }
    
    // ç¼“å­˜æ•ˆç‡è¯„åˆ†
    const cacheHitRate = this.metrics.cacheHits / ((this.metrics.cacheHits + this.metrics.cacheMisses) || 1);
    if (cacheHitRate < 0.6) {
      score -= 15; // ç¼“å­˜å‘½ä¸­ç‡ä½äº60%æ‰£15åˆ†
    } else if (cacheHitRate < 0.8) {
      score -= 8; // ç¼“å­˜å‘½ä¸­ç‡ä½äº80%æ‰£8åˆ†
    }
    
    // é”™è¯¯å’Œå›é€€è¯„åˆ†
    score -= this.metrics.errors.length * 5; // æ¯ä¸ªé”™è¯¯æ‰£5åˆ†
    score -= this.metrics.fallbacksUsed * 3; // æ¯æ¬¡å›é€€æ‰£3åˆ†
    
    // ç¡®ä¿åˆ†æ•°åœ¨0-100ä¹‹é—´
    score = Math.max(0, Math.min(100, score));
    
    if (score >= 90) return 'A+';
    if (score >= 80) return 'A';
    if (score >= 70) return 'B';
    if (score >= 60) return 'C';
    if (score >= 50) return 'D';
    return 'F';
  }

  /**
   * ç”Ÿæˆä¼˜åŒ–å»ºè®®
   */
  generateOptimizationSuggestions() {
    const suggestions = [];
    
    // åŠ è½½æ—¶é—´å»ºè®®
    if (this.metrics.totalLoadingTime > 2000) {
      suggestions.push({
        category: 'åŠ è½½æ€§èƒ½',
        suggestion: 'è€ƒè™‘å®ç°æ•°æ®åˆ†é¡µæˆ–å¢é‡åŠ è½½',
        priority: 'high',
        impact: 'å‡å°‘åˆå§‹åŠ è½½æ—¶é—´50%+'
      });
    }
    
    // ç½‘ç»œè¯·æ±‚å»ºè®®
    const bulkRatio = this.metrics.bulkRequests / (this.metrics.networkRequests || 1);
    if (bulkRatio < 0.8) {
      suggestions.push({
        category: 'ç½‘ç»œä¼˜åŒ–',
        suggestion: 'å¢åŠ æ‰¹é‡APIçš„ä½¿ç”¨ï¼Œå‡å°‘å•ç‹¬è¯·æ±‚',
        priority: 'high',
        impact: 'å‡å°‘ç½‘ç»œè¯·æ±‚80%+'
      });
    }
    
    // ç¼“å­˜å»ºè®®
    const cacheHitRate = this.metrics.cacheHits / ((this.metrics.cacheHits + this.metrics.cacheMisses) || 1);
    if (cacheHitRate < 0.7) {
      suggestions.push({
        category: 'ç¼“å­˜ä¼˜åŒ–',
        suggestion: 'ä¼˜åŒ–ç¼“å­˜ç­–ç•¥ï¼Œå¢åŠ ç¼“å­˜æ—¶é—´æˆ–æ”¹è¿›ç¼“å­˜é”®è®¾è®¡',
        priority: 'medium',
        impact: 'æé«˜å“åº”é€Ÿåº¦30%+'
      });
    }
    
    // é”™è¯¯å¤„ç†å»ºè®®
    if (this.metrics.errors.length > 0) {
      suggestions.push({
        category: 'é”™è¯¯å¤„ç†',
        suggestion: 'æ”¹è¿›é”™è¯¯å¤„ç†å’Œé‡è¯•æœºåˆ¶',
        priority: 'medium',
        impact: 'æé«˜ç³»ç»Ÿç¨³å®šæ€§'
      });
    }
    
    // å›é€€å»ºè®®
    if (this.metrics.fallbacksUsed > 0) {
      suggestions.push({
        category: 'å…¼å®¹æ€§',
        suggestion: 'ç¡®ä¿æ‰¹é‡APIçš„å¯ç”¨æ€§ï¼Œå‡å°‘å›é€€æ–¹æ¡ˆçš„ä½¿ç”¨',
        priority: 'low',
        impact: 'æé«˜æ€§èƒ½ä¸€è‡´æ€§'
      });
    }
    
    return suggestions;
  }

  /**
   * å¯¼å‡ºæ€§èƒ½æ•°æ®
   */
  exportMetrics() {
    return {
      metrics: this.metrics,
      report: this.generatePerformanceReport(),
      exportTime: new Date().toISOString()
    };
  }

  /**
   * é‡ç½®æ‰€æœ‰æŒ‡æ ‡
   */
  reset() {
    this.metrics = {
      loadingStartTime: null,
      loadingEndTime: null,
      totalLoadingTime: 0,
      plantsLoaded: 0,
      annotationsLoaded: 0,
      notesLoaded: 0,
      networkRequests: 0,
      bulkRequests: 0,
      individualRequests: 0,
      totalDataTransferred: 0,
      cacheHits: 0,
      cacheMisses: 0,
      cacheSize: 0,
      errors: [],
      fallbacksUsed: 0,
      timeToFirstInteraction: 0,
      timeToFullyLoaded: 0
    };
    
    this.isMonitoring = false;
    this.startTime = null;
    this.checkpoints = [];
    
    console.log('[æ€§èƒ½ç›‘æ§] æŒ‡æ ‡å·²é‡ç½®');
  }
}